const engineReaBlock = indentNormalizer(`
import enigma from "enigma.js";
import usePromise from "react-use-promise";
import { configEnv, config } from "./config";
import BarChart from "./BarChart";

const useGlobal = (session) => usePromise(() => session.open(), [session]);

const useSessionApp = (global) => {
  const [sessionApp] = usePromise(async () => {
    if (!global) return null;
    const app = await global.openDoc(configEnv.app.id);
    return app;
  }, [global]);
  return sessionApp;
};

function App() {
  const session = useMemo(() => enigma.create(config), []);
  const [global] = useGlobal(session);
  const app = useSessionApp(global);

  return (
    <div className="App">
      <header className="App-header"></header>
      <BarChart app={app} />
    </div>
  );
}

export default App;

///------Barchart.jsx
import React, { useEffect, useRef } from "react";
import picasso from "picasso.js";
import picassoQ from "picasso-plugin-q";
import { useModel, useLayout, usePicasso } from "hamus.js";

import { barDef } from "./data";
import { settings } from "./settings";

picasso.use(picassoQ);

const BarChart = ({ app }) => {
  const element = useRef(null);
  const [barModel] = useModel(app, barDef);
  const [barLayout] = useLayout(barModel);

  const pic = usePicasso(element, settings, barLayout);

  useEffect(() => {
    if (!app && !pic) return;
    console.log(app);
  }, [app, pic]);

  return <div className="barchart" ref={element} />;
};

export default BarChart;

`);

